.ant-layout {
  background: white;
}
body {
  margin: 40px;
  color: rgba(0, 0, 0, 0.65);
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-variant: tabular-nums;
  line-height: 1.5;
  background-color: #fff;
  font-feature-settings: "tnum";
}
.content-markdown h1,
.content-markdown h2,
.content-markdown h3,
.content-markdown h4,
.content-markdown h5,
.content-markdown h6 {
  font-weight: 400;
}
.content-markdown h1 {
  font-size: 32px;
}
.content-markdown h2 {
  font-size: 24px;
}
.content-markdown h3 {
  font-size: 18.72px;
  border-bottom: 1px dashed #d6dbdf;
  padding-bottom: 15px;
}
.content-markdown h4 {
  font-size: 16px;
}
.content-markdown h5 {
  font-size: 13.28px;
}
.content-markdown h6 {
  font-size: 12px;
}
.content-markdown a {
  text-decoration: none;
}
.content-markdown p {
  margin: 12px 0;
  line-height: 26px;
}
.content-markdown table {
  border-collapse: collapse;
}
.content-markdown td,
.content-markdown th {
  border: 1px solid #D6DBDF;
  padding: 10px 20px;
}
.content-markdown blockquote {
  margin: 20px 0;
  padding: 10px 15px;
  border-left-style: solid;
  border-left-width: 5px;
  border-color: #D6DBDF;
  background: none repeat scroll 0 0 rgba(102, 128, 153, 0.05);
}
.content-markdown blockquote p {
  margin: 10px 0;
  font-weight: 300;
}
.content-markdown code {
  font-family: Monaco, Menlo, Consolas, "Courier New", monospace;
  border: 0;
  background-color: rgba(214, 219, 223, 0.7);
  border-radius: 3px;
  color: rgba(44, 62, 80, 0.8);
  font-size: 90%;
  padding: 2px 4px;
  white-space: nowrap;
}
.content-markdown code[class^="language-"] {
  white-space: unset;
  display: block;
  padding: 20px;
  background-color: #f7f8fa;
  overflow: auto;
}
.content-markdown hr {
  border-style: solid;
  border-width: 1px;
  border-bottom: none;
  color: #d6dbdf;
  margin: 30px 0;
}
